const CITIES_DATA = [
    { id: '11201', lat: 35.9251, lng: 139.4858, radius: 7000 }, // Â∑ùË∂äÂ∏Ç
    { id: '11208', lat: 35.7994, lng: 139.4687, radius: 7000 }, // ÊâÄÊ≤¢Â∏Ç
    { id: '11215', lat: 35.8533, lng: 139.4122, radius: 5000 }, // Áã≠Â±±Â∏Ç (ËøΩÂä†)
    { id: '11245', lat: 35.8794, lng: 139.5197, radius: 4000 } // „Åµ„Åò„ÅøÈáéÂ∏Ç (ËøΩÂä†)
];

function calculateDistance(lat1: number, lng1: number, lat2: number, lng2: number): number {
    const R = 6371000;
    const dLat = (lat2 - lat1) * (Math.PI / 180);
    const dLng = (lng2 - lng1) * (Math.PI / 180);
    const a =
        Math.sin(dLat / 2) * Math.sin(dLat / 2) +
        Math.cos(lat1 * (Math.PI / 180)) * Math.cos(lat2 * (Math.PI / 180)) * Math.sin(dLng / 2) * Math.sin(dLng / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    return R * c;
}

function determineCityCode(lat: number, lng: number): string | null {
    for (const city of CITIES_DATA) {
        const dist = calculateDistance(lat, lng, city.lat, city.lng);
        if (dist <= city.radius) return city.id;
    }
    return null;
}

function MapControllerLogic({ setCenter, setZoom }: { setCenter: (pos: { lat: number; lng: number }) => void; setZoom: (z: number) => void }) {
    const searchParams = useSearchParams();
    useEffect(() => {
        const latParam = searchParams.get('lat');
        const lngParam = searchParams.get('lng');
        const zoomParam = searchParams.get('zoom');
        if (latParam && lngParam) {
            const lat = parseFloat(latParam);
            const lng = parseFloat(lngParam);
            if (!isNaN(lat) && !isNaN(lng)) {
                setCenter({ lat, lng });
                if (zoomParam) {
                    const z = parseInt(zoomParam, 10);
                    if (!isNaN(z)) setZoom(z);
                }
            }
        }
    }, [searchParams, setCenter, setZoom]);

    useEffect(() => {
        const currentParams = new URLSearchParams(window.location.search);
        if (currentParams.has('lat') && currentParams.has('lng')) return;
        const savedPos = localStorage.getItem(STORAGE_KEY);
        if (savedPos) {
            try {
                const parsed = JSON.parse(savedPos);
                if (parsed.lat && parsed.lng) {
                    setCenter({ lat: parsed.lat, lng: parsed.lng });
                    if (parsed.zoom) setZoom(parsed.zoom);
                }
            } catch (e) {
                console.error(e);
            }
        }
    }, []);
    return null;
}

